import {
  Box,
  Flex,
  Spacer,
  Show,
  Hide,
  Center,
  Heading,
  Text,
} from "@chakra-ui/react";

import React from "react";
// import { HashLink as Link } from "react-router-hash-link";
import { Link } from "react-scroll";
import SideNav from "./SideNav";

export default function Navbar() {
  return (
    <Flex
      w="full"
      alignItems="center"
      gap="2"
      p={5}
      pl="40px"
      pr="40px"
      justifyContent="space-between"
      bg="transparent"
    >
      <Box>
        <Link
          to="/"
          activeClass="active"
          spy={true}
          smooth={true}
          duration={1000}
          activeClassName="selected"
        >
          <Heading>Protfolio </Heading>
        </Link>
      </Box>
      <Spacer />
      <Show breakpoint="(min-width: 1000px)">
        <Flex gap="5">
          <Center>
            <Link
              to="/"
              activeClass="active"
              spy={true}
              smooth={true}
              
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Home </Text>
            </Link>
          </Center>
          <Center>
            <Link
              activeClass="active"
              to="about"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">About </Text>
            </Link>
          </Center>
          <Center>
            <Link
              to="project"
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Projects </Text>
            </Link>
          </Center>
          <Center>
            <Link
              to="contact"
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Contacts </Text>
            </Link>
          </Center>

          <Link to="resume">
            <Center>
              <Text fontSize="1.5xl">Resume </Text>
            </Center>
          </Link>
        </Flex>
      </Show>
      <Hide above="1000px">
        <SideNav />
      </Hide>
      <Box></Box>
    </Flex>
  );
}
